.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  background: #6a4944;
  color: white;
}

.navbar-menu {
  display: flex;
  align-items: center;
  gap: 50px;
  font-size: 20px;
  font-weight: 500;
  list-style: none;
}

.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
}

.navbar-cart{
  display: flex;
  align-items: center;
}

.navbar-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -10px;
  border-radius: 11px;
  font-size: 14px;
  background: red;
  color: white;
}

